.preroll-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 20000;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.3s ease-in-out;
}

.preroll-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.preroll-counter {
  font-size: 180px;
  font-weight: 700;
  color: #f8d54e;
  text-shadow: 0 0 20px rgba(248, 213, 78, 0.7);
  margin-bottom: 30px;
  transition: all 0.2s ease;
  opacity: 0;
  transform: scale(1.5);
}

.preroll-counter.animate {
  animation: beatPulse 0.5s ease-out forwards;
}

.preroll-progress {
  display: flex;
  gap: 10px;
}

.progress-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  transition: all 0.2s ease;
}

.progress-dot.active {
  background-color: #f8d54e;
  box-shadow: 0 0 10px rgba(248, 213, 78, 0.7);
  transform: scale(1.2);
}

.progress-dot.passed {
  background-color: rgba(248, 213, 78, 0.5);
}

@keyframes beatPulse {
  0% {
    transform: scale(1.5);
    opacity: 0.9;
  }
  100% {
    transform: scale(0.8);
    opacity: 0.7;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Добавляем класс для анимации исчезновения */
.preroll-overlay.fading-out {
  animation: fadeOut 0.3s ease-in-out forwards;
}

/* Анимация исчезновения */
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
} 