.vertical-view-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 10000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  overflow: hidden;
}

.vertical-view-content {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.vertical-view-player-menu {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* Кнопка закрытия вертикального режима */
.vertical-mode-toggle {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 10001;
}

.vertical-mode-close-btn {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 18px;
  transition: all 0.2s ease;
}

.vertical-mode-close-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.05);
}

/* Переопределение стилей для VerticalView внутри оверлея */
.vertical-view-overlay .vertical-view-container {
  position: relative;
  top: auto;
  left: auto;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
} 