.sequencer-module {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: relative;
  transition: all 0.5s ease-in-out;
}

/* Стили для вертикального режима */
.sequencer-module.vertical-mode {
  background-color: #000;
}

/* Стили для анимации входа в вертикальный режим */
.sequencer-module.vertical-view-enter {
  animation: magicEnter 0.7s ease-in-out;
}

/* Скрываем лишние элементы в вертикальном режиме */
.vertical-mode .horizontal-only {
  display: none;
}

/* Анимация для магического эффекта при переходе в вертикальный режим */
@keyframes magicEnter {
  0% {
    transform: scale(0);
    opacity: 0;
    filter: blur(10px);
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
    filter: blur(4px);
  }
  100% {
    transform: scale(1);
    opacity: 1;
    filter: blur(0);
  }
} 