.time-signature-value {
  padding: 0 5px; /* Немного отступов вокруг значения */
  min-width: 1em; /* Минимальная ширина для цифры */
  text-align: center;
  font-weight: bold;
  color: #fff; /* Чтобы значение было хорошо видно */
}

.time-signature-step-button {
  background-color: #555;
  color: white;
  border: 1px solid #777;
  border-radius: 4px; /* Совпадает с другими кнопками */
  padding: 0;
  width: 26px; /* Подбираем размер для умещения в 42px высоты панели */
  height: 26px;
  font-size: 1.1em; /* Размер для символов +/- */
  font-weight: bold;
  cursor: pointer;
  line-height: 24px; /* Для вертикального центрирования текста */
  text-align: center;
  transition: background-color 0.2s;
}

.time-signature-step-button:hover {
  background-color: #6a6a6a;
}

.time-signature-step-button:disabled {
  background-color: #444;
  color: #888;
  cursor: not-allowed;
}

.time-signature-denominator, .bar-controls-label {
  white-space: nowrap; 
}

.grid-control-button {
  background-color: #555;
  color: white;
  border: 1px solid #777;
  border-radius: 3px;
  padding: 3px 7px; 
  font-size: 0.9em;
  cursor: pointer;
  min-width: 22px;
  text-align: center;
  line-height: 1.2; 
}

.grid-control-button:hover {
  background-color: #6a6a6a;
}

/* .instrument-panel остается flex-контейнером */

/* Дополнительно: Убедимся, что кнопки +/- в .bar-controls также хорошо выглядят */
.bar-controls .grid-control-button {
  font-weight: bold;
  width: 26px; /* Можно сделать их по размеру как time-signature-step-button для единообразия */
  height: 26px;
  line-height: 24px;
  padding: 0;
}

/* Стили для контейнера кнопок управления блоками (если он был для block-controls внутри grid-panel) */
.block-controls { /* Этот класс используется для bar-controls в JSX, переименуем или убедимся в консистентности */
  display: flex;
  align-items: center; 
  gap: 8px; /* Уже установлено в .bar-controls, можно удалить дублирование */
  /* margin-bottom: 10px; /* Убрано, так как bar-controls в одну линию */
}

.block-controls-label { /* Это .bar-controls-label */
  font-size: 0.9em;
  margin-right: 4px; 
}

.add-block-button, .remove-block-button {
  /* Стили для этих классов не используются напрямую, если кнопки имеют класс grid-control-button */
}