/* Sequencer.css - Адаптивный дизайн с фиксированными размерами для разных устройств */

/* Контейнер для всех блоков старая версия */
.blocks-container {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    padding: 12px;
    width: 100%;
    box-sizing: border-box;
    background: transparent;
    justify-content: center;
    background-color: rgba(76, 175, 80, 0.1);
  }
  
  /* Контейнер для одного блока - размеры подбираются под устройство */
  .block-container {
    box-sizing: border-box;
    margin-bottom: 1px;
    /* Базовые размеры устанавливаются в медиа-запросах */
  }
  
/* Стили для секвенсера */
.sequencer {
    border-radius: 6px;
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
    background: transparent; /* Прозрачный фон */
    border: none; /* Убрана граница */
  }
  
  
  /* Главная часть секвенсера */
  .sequencer-main {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  
  /* Стили для заголовка секвенсера */
  .sequencer-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    color: #d9c6ff;
  }
  
  /* Стили для заголовка блока */
  .sequencer-header h2 {
    margin: 0;
    font-weight: 600;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #c8b3e8;
  }
  
  /* Стили для кнопки LOOP */
  .loop-button {
    background-color: #7B42FF;
    color: white;
    border: none;
    border-radius: 4px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s, transform 0.1s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .loop-button:hover {
    background-color: #8b4edc;
    transform: translateY(-1px);
  }
  
  .loop-button:active {
    transform: translateY(0);
  }
  
  /* Сетка для секвенсера */
  .grid {
    display: flex;
    flex-direction: column;
    gap: 3px;
    width: 100%;
  }
  
  /* Стили для ряда трека */
  .track-row {
    display: flex;
    gap: 3px;
    width: 100%;
  }
  
/* Стили для ячейки сетки */
.grid-cell {
  flex: 1;
  aspect-ratio: 1/1;
  background: transparent;
  border: 1px solid #364156; /* Голубая обводка */
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.15s ease;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
}
  
 /* .grid-cell:hover {
    border-color: #32F9E5; ]
    box-shadow: 0 0 3px rgba(52, 152, 219, 0.4); /* Легкое свечение при наведении
  } */

  .grid-cell.active {
    border-color: #32F9E5;
  }
  .instrument-indicator {
    font-size: 0.5em;
    color: #fff;
  }
  
  .block-container.inactive {
    opacity: 0.6;
  }
  
  .track-row.muted {
    opacity: 0.4;
  }
  
  .loop-button.active {
    background-color: #4CAF50;
    color: white;
  }
  
  .cell-instrument-icon {
    width: 70%;
    height: 70%;
    object-fit: contain;
      /* Убедимся, что иконка не сдвинется от центра */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
  
  
  /* Стили для долгого нажатия */
  .track-row {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }
  
/* Стили для активных и проигрываемых ячеек */

/* Активная ячейка (имеет инструмент) */
.grid-cell.active {
  background: transparent;
  border: 1px solid #32F9E5;
  color: white;
}

/* Отключаем анимацию и стили проигрывания для ячеек в замьютированном ряду */
.track-row.muted .playing-active,
.track-row.muted .playing-empty {
  animation: none !important;
  box-shadow: none !important;
  border-color: #364156 !important; /* Возвращаем стандартный бордер */
  transform: none !important; /* Убираем scale */
  background: transparent !important; /* Возвращаем стандартный фон */
}

/* Модифицируем класс для проигрываемых ячеек с учетом разницы между пустыми и заполненными */

/* Общие стили для проигрываемых ячеек */
.grid-cell.playing-active, .grid-cell.playing-empty {
  transition: all 0.15s ease;
  transform: scale(1.05);
  z-index: 10;
}

/* Ячейка с инструментом, которая проигрывается в данный момент */
.grid-cell.playing-active {
  background: transparent;
  border: 1px solid #F9CB40;
  box-shadow: 0 0 12px rgba(249, 203, 64, 0.9);
  animation: pulse-active 0.2s infinite alternate;
}

/* Пустая ячейка, которая проигрывается в данный момент */
.grid-cell.playing-empty {
  background: transparent;
  border: 1px solid #F9CB40;
  box-shadow: 0 0 6px rgba(249, 203, 64, 0.4);
  animation: pulse-empty 0.2s infinite alternate;
}

/* Анимация пульсации для проигрываемых ячеек с инструментом */
@keyframes pulse-active {
  from {
    opacity: 1;
    transform: scale(1.05);
  }
  to {
    opacity: 0.9;
    transform: scale(1.15);
  }
}

/* Анимация пульсации для пустых проигрываемых ячеек */
@keyframes pulse-empty {
  from {
    opacity: 0.9;
    transform: scale(1.02);
  }
  to {
    opacity: 0.7;
    transform: scale(1.05);
  }
}

/* Удаляем устаревший класс .playing, так как теперь у нас есть более специфичные классы */
.grid-cell.playing {
  display: none;
}

/* Активный блок (текущий блок в последовательности) */
.block-container.active {
  transition: all 0.3s ease;
  /*box-shadow: 0 0 15px rgba(78, 110, 142, 0.5); */
  opacity: 1;
}

/* Неактивный блок (другие блоки) */
.block-container.inactive {
  opacity: 0.7;
  transition: all 0.3s ease;
}

/* Стиль для блоков вне активного лупа */
.block-out-of-loop {
  opacity: 0.4;
  transition: all 0.3s ease;
}

.cells-container {
  display: flex;
  width: 900px; /* Фиксированная ширина контейнера */
  height: 60px;
  overflow-x: auto; /* Добавляем возможно */
}

/* Улучшенные стили для транспортных контролов */
.transport-controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #f0f0f0;
  border-radius: 5px;
}

.transport-controls button {
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  background-color: #333;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s;
}

.transport-controls button:hover {
  background-color: #555;
}

/* Улучшенный стиль индикатора инструмента */
.instrument-indicator {
  font-size: 10px;
  font-weight: bold;
}
/* Добавить в существующий CSS файл */
@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0.3; }
  100% { opacity: 1; }
}

  
