  }
  
  .grid-cell.current-step::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(76, 175, 80, 0.3);
    z-index: 1;
    pointer-events: none;
  }
  
