/* Стили для управления количеством шагов в дорожке (GRID режим) */
.track-step-controls {
  display: flex;
  align-items: center;
  gap: 4px; /* Расстояние между элементами */
  margin-bottom: 4px; /* Отступ снизу, если контролы над ячейками */
  /* Если контролы слева от ячеек, то margin-right можно добавить */
  /* padding: 2px 0; */ /* Небольшой внутренний отступ */
}

.step-control-button {
  background-color: #555; /* Темный фон для кнопок */
  color: white;
  border: 1px solid #777;
  border-radius: 4px;
  padding: 2px 6px; /* Компактные паддинги */
  font-size: 0.9em;
  cursor: pointer;
  min-width: 20px; /* Минимальная ширина для кнопок +/- */
  text-align: center;
}

.step-control-button:hover {
  background-color: #666;
}

.step-control-button:disabled {
  background-color: #444;
  color: #888;
  cursor: not-allowed;
}

.step-count-input {
  width: 40px; /* Ширина для инпута */
  padding: 3px 5px;
  border: 1px solid #777;
  border-radius: 4px;
  background-color: #333; /* Темный фон для инпута */
  color: white;
  text-align: center;
  font-size: 0.9em;
}

/* Убираем стрелочки у input type=number в Chrome, Safari, Edge, Opera */
.step-count-input::-webkit-outer-spin-button,
.step-count-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Убираем стрелочки у input type=number в Firefox */
.step-count-input[type=number] {
  -moz-appearance: textfield;
}

