.player-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: #282828;
  border-radius: 5px;
  margin-top: 20px;
  position: relative;
  z-index: 1100; /* Выше чем у вертикального отображения */
}

/* Стили для PlayerMenu в вертикальном режиме */
.player-menu.in-vertical-mode {
  margin-top: 0;
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding: 10px;
}

.transport-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.play-button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s;
}

/* Компактная кнопка в вертикальном режиме */
.in-vertical-mode .play-button {
  width: 40px;
  height: 40px;
  padding: 0;
  border-radius: 50%;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.play-button:hover {
  background-color: #45a049;
}

.play-button.active {
  background-color: #f44336;
}

.play-button.active:hover {
  background-color: #d32f2f;
}

.display-mode-button {
  padding: 10px 15px;
  background-color: #2196F3;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s ease-in-out;
}

.display-mode-button:hover {
  background-color: #0b7dda;
}

.display-mode-button.active {
  background-color: #673AB7;
}

.display-mode-button.active:hover {
  background-color: #5e35b1;
}

.bpm-controls {
  display: flex;
  align-items: center;
  gap: 10px;
  color: white;
}

/* Компактный контроль BPM в вертикальном режиме */
.in-vertical-mode .bpm-controls {
  gap: 5px;
}

#bpm-input {
  width: 60px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
}

.in-vertical-mode #bpm-input {
  width: 50px;
  padding: 3px;
  font-size: 14px;
}

.bpm-slider {
  width: 100px;
  cursor: pointer;
}

.in-vertical-mode .bpm-slider {
  width: 80px;
}

.bar-controls {
  display: flex;
  align-items: center;
  gap: 10px;
  color: white;
}

.in-vertical-mode .bar-controls {
  gap: 5px;
}

.bar-button {
  padding: 5px 10px;
  background-color: #555;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.in-vertical-mode .bar-button {
  width: 30px;
  height: 30px;
  padding: 0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bar-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.bar-indicator {
  min-width: 80px;
  text-align: center;
}

.in-vertical-mode .bar-indicator {
  min-width: 50px;
  font-size: 14px;
}

.preroll-button {
  background-color: #333;
  color: white;
  border: 1px solid #555;
  padding: 5px 10px;
  margin-left: 10px;
  border-radius: 3px;
  font-size: 14px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.preroll-button.active {
  background-color: #772;
  border-color: #aa3;
  color: #fff;
}

.preroll-button:hover {
  background-color: #444;
}

.preroll-button.active:hover {
  background-color: #993;
}

/* Стили для кнопки метронома */
.metronome-button {
  background-color: #333;
  color: white;
  border: 1px solid #555;
  padding: 5px 10px;
  margin-left: 10px;
  border-radius: 3px;
  font-size: 14px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.metronome-button:hover {
  background-color: #444;
}

.metronome-button.active {
  background-color: #2a7;
  border-color: #3b8;
  color: #fff;
}

.metronome-button.active:hover {
  background-color: #3b8;
}

/* Адаптивность для мобильных устройств */
@media (max-width: 768px) {
  .player-menu {
    flex-direction: column;
    gap: 10px;
    padding: 10px;
  }
  
  .transport-controls,
  .bpm-controls,
  .bar-controls {
    width: 100%;
    justify-content: center;
  }
  
  /* В вертикальном режиме на мобильных устройствах сохраняем строчный вид */
  .in-vertical-mode.player-menu {
    flex-direction: row;
    align-items: center;
  }
} 